* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}

.header {
  background-image: linear-gradient(to right, #1e3a8a, #1d4ac9);
  color: white;
  display: grid;
  grid-template-rows: max-content 1fr;
  grid-template-areas: "nav" "content";
  min-height: 100vh;
}

.nav {
  grid-area: nav;
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  justify-content: space-between;
  padding-top: 20px;
  height: 90px;
}

.nav__picture {
  width: 33px;
  height: 33px;
}

.nav__img {
  width: 100%;
}

.nav__links {
  display: grid;
  grid-auto-flow: column;
  gap: 10px;
}

.nav__link {
  font-weight: 500;
  padding: 8px 20px;
  border-radius: 5px;
}

.nav__link--border {
  border: 1px solid white;
}

.header__main {
  grid-area: content;
  padding-bottom: 180px;
}

.info__container {
  text-align: center;
}

.info__title {
  font-size: 45px;
  letter-spacing: 2px;
}

.info__paragraph {
  margin-top: 10px;
  margin-bottom: 35px;
  font-size: 20px;
}

.info__btn {
  padding: 13px 40px;
  background-image: linear-gradient(to right, #1e3a8a, #1d4ac9);
  border-radius: 5px;
  margin-bottom: 25px;
}

.header__picture {
  margin: 0 auto;
  padding-top: 40px;
  max-width: 500px;
}

.header__img {
  width: 100%;
}

.header__wave {
  grid-area: content;
  align-self: flex-end;
}

@media (min-width: 800px) {
  .header__main {
    display: grid;
    grid-auto-flow: column;
    align-items: center;
    justify-content: space-between;
    padding-top: 50px;
  }

  .header__picture {
    padding-top: 0px;
  }
}
